@media only screen and (max-width: 589px){
  .collection-switcher__collection-grid{
    display: none;
  }

 .slider {
   display: flex;
   overflow-x: auto;
   width: 100%;
   height: 410px;
   overflow-y: hidden;
   -webkit-overflow-scrolling: touch;
   scroll-snap-type: x mandatory;
 }

 .slider > div {
  scroll-snap-align: start;
 }

 .slider .grid-product__content{
  width: 210px;
 }

 .slider .small--one-half{
   width: 90%;
 }

 .slider img{
   height: 300px;
   object-fit: cover;
 }

 .slider .grid-product__image-mask{
   height: 300px;
 }

}
