
.modalgeo {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 999999999; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 740px; /* Could be more or less, depending on screen size */
}

.pop-title{
  width: 430px;

}

.inner-content{
  text-align: center;
}

.inner-content p{
  margin: 13px 130px 20px;
}

/* The Close Button */
.close-geopop {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  display: flex;
  justify-content: flex-end;
}

.close-geopop:hover,
.close-geopop:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

@media only screen 
  and (max-device-width: 612px) {
    .modal-content{width: 90%;}
    .pop-title{width: 300px;}
    .inner-content p {margin: 13px 0px 20px;}
    #Name, #country{width: 100%;}
    .country-select{width: 100%; margin-bottom: 1rem;}
    .ship-amerika{tex-align: center}
  }

  @media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) {
    .modal-content{
      width: 95%;
    }
  }

  @media only screen 
  and (min-device-width: 834px) 
  and (max-device-width: 1112px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .modal-content{
      width: 70%;
    }
  }
