/* @tailwind base; */

@tailwind components;
@tailwind utilities;

.add-and-buy .btn.add-to-cart {
  color: #000;
}
.add-and-buy .btn.add-to-cart:hover {
  color: #fff;
}

.announce-icon .a {
	stroke: white;
}

@media only screen and (max-width: 589px) {
  #product_notify_form input[type='email'] {
    width: 100%;
  }

  #product_notify_form button[type='submit'] {
    display: block;
    width: 100%;
    margin-top: 1rem;
  }

  #product_notify_form span.form__submit--large {
    display: block;
  }
}

/* purgecss start ignore */
/* VWO tests - Quick View On Collections All*/
body.no-quick-shop .quick-product__btn {
	display: none;
}

/* VWO tests - You May Also Like */
body.no-also-like div[data-section-type="product-recommendations"] {
	display: none;
}

/* VWO tests - Chat with sizing expert */
.chat-experts {
  display: none;
}

body.show-chat-experts .chat-experts {
  display: block;
}
/* purgecss end ignore */
